class Util {
  constructor() {}

  /*
   * @description çæéæºid
   */
  createRandomId() {
    return `${Math.random() * 10}-${new Date().getTime()}-${Math.random() * 10}`;
  }
  //çæéæºæ±å­
  randomlyCharacters(num) {
    let arr = [];
    for (let i = 0; i < num; i++) {
      let str;
      //æ±å­å¯¹åºçunicodeç¼ç ä¸ºu4e00-u9fa5è½¬ä¸º10è¿å¶ä¸º19968-40869ï¼ååéæºæ°ï¼åè½¬ä¸º16è¿å¶
      str = '\\u' + (Math.floor(Math.random() * (40869 - 19968)) + 19968).toString(16);
      //å¨ç¨æ­£åæä½æ°æ®åè¿è¡è§£ç ãæ³¨æï¼unescape() å½æ°å¨ JavaScript 1.5 çä¸­å·²å¼ç¨ãè¯·ä½¿ç¨ decodeURI() æ decodeURIComponent() ä»£æ¿ã
      str = unescape(str.replace(/\\u/g, '%u'));
      arr.push(str);
    }
    let chinese = arr.join('');
    return chinese;
  }
}

export default new Util();
